.navigation {
  position: absolute;
  top: 0.625rem;
  right: 6.25rem;
}
.navigation .navigation__list {
  display: flex;
  flex-direction: row;
}
.navigation .navigation__item {
  position: relative;
  list-style: none;
  font-size: var(--fs-links);
  text-transform: uppercase;
  font-weight: 600;
  padding-right: 3.25rem;
  width: initial;
  transition: transform 0.3s ease-in-out;
}
.navigation .navigation__item:hover {
  transform: scale(1.05);
}
.navigation .navigation__link {
  color: var(--color-dark);
  text-decoration: none;
}
.navigation .navigation__link:hover .navigation__highlight {
  width: 6.25rem;
}
.navigation .navigation__hamburger {
  display: none;
}
@media (max-width: 767px) {
  .navigation--open {
    background-color: var(--color-primary);
    opacity: 95%;
    top: 0;
    left: 0;
    width: 100%;
    height: 80vh;
    animation: slideInLeft 0.5s;
    position: fixed;
    z-index: 1;
  }
  .navigation .navigation__list {
    display: none;
  }
  .navigation .navigation__list--open {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding-top: 4rem;
    align-items: center;
  }
  .navigation .navigation__item {
    font-size: 2rem;
  }
  .navigation .navigation__item:hover {
    transform: none;
  }
  .navigation .navigation__hamburger {
    display: flex;
    font-size: 2rem;
    position: fixed;
    right: 0.75rem;
    z-index: 1;
  }
  .navigation .navigation__hamburger--open {
    padding-top: 1rem;
    font-size: 2.2rem;
  }
  .navigation .navigation__link:hover .navigation__highlight {
    width: 10rem;
    height: 2.5rem;
    background-color: var(--color-primary-dark);
  }
}

@keyframes slideInLeft {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}/*# sourceMappingURL=Navigation.css.map */