.project {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1.5rem;
  width: 85%;
  margin-bottom: 2rem;
  border: var(--border-dark);
  border-radius: 20px;
  box-shadow: 6px 6px 0 -1px var(--color-primary), 6px 6px 0 var(--color-dark);
}
.project img {
  width: 100%;
  height: 100%;
}
.project .project__stack {
  list-style: none;
  display: flex;
  gap: 1rem;
  padding: 0;
}
.project .project__links-container {
  display: flex;
  gap: 3rem;
  position: relative;
}
.project .project__link:first-child:hover ~ .project__highlight {
  width: 100px;
}
.project .project__link:nth-child(2):hover ~ .project__highlight {
  left: unset;
  right: 0;
  width: 100px;
}
@media (max-width: 767px) {
  .project .project__stack {
    flex-direction: column;
    align-items: center;
  }
  .project .project__links-container {
    gap: 2rem;
  }
}/*# sourceMappingURL=ProjectItem.css.map */