.header {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 6.25rem;
}
.header__text-container {
  text-align: left;
  max-width: 60%;
}
.header p {
  font-size: var(--fs-body);
  margin-bottom: 6.25rem;
}
.header span {
  background-color: var(--color-primary-light);
  background-image: linear-gradient(-100deg, var(--color-primary-light) 65%, var(--color-primary));
}
.header .header__img-container {
  display: flex;
  align-items: center;
  justify-content: center;
  align-items: center;
  width: 28.75rem;
  height: 29.37rem;
}
.header .header__img-container--mobile {
  display: none;
}
.header .header__img-background {
  position: absolute;
  z-index: -1;
}
.header .header__img-background path {
  fill: var(--color-primary);
}
.header .header__img {
  width: 19.375rem;
  border: var(--border-dark);
  border-radius: 3.75rem;
  margin: 0 2rem 1.25rem 0;
  transition: transform 0.3s ease-in-out;
}
.header .header__img:hover {
  transform: scale(1.05);
}
@media (max-width: 767px) {
  .header {
    padding-top: var(--padding-mobile);
  }
  .header p {
    font-size: var(--fs-body-mobile);
    margin-bottom: 4.24rem;
  }
  .header .header__img-container {
    width: auto;
    height: auto;
    max-width: 19rem;
  }
  .header .header__img {
    margin: 0;
    width: 85%;
  }
  .header .header__img-background {
    display: none;
  }
}
@media (max-width: 1025px) {
  .header {
    justify-content: center;
  }
  .header .header__text-container {
    max-width: 100%;
  }
  .header .header__img-container--desktop {
    display: none;
  }
  .header .header__img-container--mobile {
    display: flex;
    margin: 0 auto;
  }
}/*# sourceMappingURL=Header.css.map */