.contact {
  display: flex;
  flex-direction: column;
  background-color: var(--color-primary-light);
  position: relative;
  padding-top: 0;
  align-items: end;
}
.contact .contact__container {
  padding-right: 5%;
}
.contact p {
  font-size: 2.2rem;
  margin: 0;
}
.contact .contact__wave {
  position: absolute;
  transform: scaleX(-1);
  z-index: -1;
  bottom: 166px;
  left: 0;
}
.contact .contact__wave path {
  fill: var(--color-primary-light);
}
@media (max-width: 767px) {
  .contact p {
    font-size: 1.5rem;
  }
  .contact .contact__wave {
    bottom: 100px;
  }
}
@media (max-width: 360px) {
  .contact h2 {
    font-size: 1.5rem;
  }
  .contact p {
    font-size: 1.3rem;
  }
  .contact .contact__wave {
    bottom: 90px;
  }
}/*# sourceMappingURL=Contact.css.map */