.navigation {
   position: absolute;
   top: 0.625rem;
   right: 6.25rem;

   & &__list {
      display: flex;
      flex-direction: row;
   }
    
   & &__item {
      position: relative;
      list-style: none;
      font-size: var(--fs-links);
      text-transform: uppercase;
      font-weight: 600;
      padding-right: 3.25rem;
      width: initial;
      transition: transform 0.3s ease-in-out;

      &:hover {
         transform: scale(1.05);
       }
   }

   & &__link {
      color: var(--color-dark);
      text-decoration: none;

      &:hover  .navigation__highlight{
         width: 6.25rem; 
      }
   }

   & &__hamburger {
      display: none;
   }

   @media (max-width: 767px) {
      &--open { 
         background-color: var(--color-primary);
         opacity: 95%;
         top: 0;
         left: 0;
         width: 100%;
         height: 80vh;
         animation: slideInLeft 0.5s; 
         position: fixed;
         z-index: 1;
      }
      
    
      & &__list {
         display: none;
         &--open {
            display: flex;
            flex-direction: column;
            gap: 2rem;
            padding-top: 4rem;
            align-items: center;
         }
      }

      & &__item {
         font-size: 2rem;
         &:hover {
            transform: none;
         }
      }

      & &__hamburger {
         display: flex;
         font-size: 2rem;
         position: fixed;
         right: 0.75rem;
         z-index: 1;
         &--open {
            padding-top: 1rem;
            font-size: 2.2rem
         }     
      }

      & &__link {
   
         &:hover  .navigation__highlight{
            width: 10rem; 
            height: 2.5rem;
            background-color: var(--color-primary-dark);
         }
      } 
   }
}      

@keyframes slideInLeft {
   from {
     transform: translateX(-100%);
   }
   to {
     transform: translateX(0);
   }
}