.stack {
  margin-top: 6.25rem;
  background-color: var(--color-primary-light);
  position: relative;
}
.stack .stack__wave {
  position: absolute;
  z-index: -1;
  left: 0;
}
.stack .stack__wave path {
  fill: var(--color-primary-light);
}
.stack .stack__wave--top {
  bottom: 426px;
}
.stack .stack__wave--bottom {
  rotate: 180deg;
  top: 425px;
}
.stack .stack__list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 1rem;
  list-style: none;
  text-align: center;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: 700;
}
.stack .stack__list img {
  width: 5rem;
  transition: 0.35s;
}
.stack .stack__list img:hover {
  transform: scale(1.2);
}
@media (max-width: 767px) {
  .stack .stack__list {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;
  }
  .stack .stack__wave--top {
    top: -33px;
  }
  .stack .stack__wave--bottom {
    top: auto;
    bottom: -32px;
  }
}
@media (max-width: 360px) {
  .stack .stack__wave--top {
    top: -28px;
  }
  .stack .stack__wave--bottom {
    bottom: -28px;
  }
}/*# sourceMappingURL=Stack.css.map */