.projects {
  padding-top: var(--padding-desktop-bg);
  padding-bottom: var(--padding-desktop-bg);
}
.projects .projects__container {
  display: grid;
  place-items: center;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
  padding-bottom: 2rem;
}
@media (max-width: 767px) {
  .projects {
    padding: var(--padding-mobile) 1rem;
  }
}
@media (max-width: 1025px) {
  .projects .projects__container {
    grid-template-columns: 1fr;
    padding-bottom: 0;
  }
}/*# sourceMappingURL=Projects.css.map */